import md5 from '@/md5.js'
export default {
	data() {
		return {
			userInfo: {},
			favors: []
		};
	},
	onLoad: function() {
		this.getbookshelf();
		this.userInfo = this.$store.state;
	},
	methods: {
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = "abcde";
			return [timestamp, md5(api_key + timestamp)];
			//返回api_key+时间戳md5加密
		},
		async getbookshelf() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/users/bookshelf',
				data: {
					time: key[0],
					token: key[1],
					utoken: this.$store.state.utoken
				},
				success: (res) => {
					this.favors = res.data.favors;
					console.log(res.data);
				}
			});
		},
		changenickname(){
			uni.navigateTo({
				url:'../changenickname/changenickname'
			})
		},
		showAS(){
			uni.showActionSheet({
				itemList: ['切换用户'],
				success: function (res) {
					// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					// this.gotologin();
					uni.navigateTo({
						url: '../login/login'
					})
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		}
		,
		gotochapterdetail(id) {
			uni.navigateTo({
				url: '../chapterdetail/chapterdetail?id=' + id
			})
		},
		gotocomicdetails(id, uid) {
			uni.navigateTo({
				url: '../comicdetails/comicdetails?id=' + id + '&uid=' + uid
			})
		},
		gotologin() {
			uni.navigateTo({
				url: '../login/login'
			})
		}
	}

}
